<template>
    <div class="check-details scrollTop">
        <div class="company page padding bg">
            <header class="details-header">
                <div class="title">
                    <h3 class="wrap">{{details.title}}</h3>
                </div>
                <ul>
                    <li>{{details.requestType}}</li>
                    <li>{{details.industryType}}</li>
                </ul>
                <div class="indc">
                    <p class="no-check" v-if="details.checkStatus == 1">{{$t('details.agreed')}}</p>
                    <p class="no-check" v-if="details.checkStatus == 2">{{$t('details.rejected')}}</p>
                    <p class="address">{{details.area}}</p>
                </div>
            </header>
            <div class="details-email">
                <div class="email-left">
                    <h4>{{$t('details.publishTemail')}}</h4>
                    <p>{{connector}}</p>
                </div>
            </div>
            <div class="detail-content">
                <div class="common" v-if="details.products">
                    <h3 class="title-before">{{$t('publish.product')}}</h3>
                    <p class="wrap">{{details.products}}</p>
                </div>
                <div class="common">
                    <h3 class="title-before">{{$t('publish.detail')}}</h3>
                    <div :class="['wrap', {'details-article': allShow}]">
                        <p class="article wrap">{{details.description}}</p>
                        <p class="all" v-if="allShow" @click="seeAll"><span>...</span>{{$t('details.expandAll')}}</p>
                    </div>
                    <div class="details-img">
                        <ul>
                            <li v-for="img in imgLists">
                                <img :src="img.url" alt="">
                            </li>
                        </ul>
                    </div>
                </div>
                <div class="details-company" @click="goApprove">
                    <div class="company-left" :style="'background-image: url(' + details.iconurl + ');'"></div>
                    <div class="company-right">
                        <div class="company-infos">
                            <p class="title wrap">{{details.company}}<span class="tag" v-if="details.istrust == 1">{{$t('base.trusted')}}</span></p>
                        </div>
                        <p class="web ellipsis">{{details.companyUrl}}</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="check-details-btns bg" v-if="details.checkStatus == 0">
            <p @click="check('2')" :class="{'no-click': btnStatus}">{{reject}}</p>
            <p @click="check('1')" :class="{'no-click': btnStatus}">{{agree}}</p>
            <p @click="goDetails">{{$t('details.nextItem')}}</p>
        </div>
    </div>
</template>

<script>
    import { publicPost } from '../../js/api/index.js';
    import getApiUrl from '../../js/api/apiUrlList';

    export default {
        name: 'page-details',
        data () {
            return {
                connector: '',
                details: {},
                imgLists: [],
                allShow: false,
                reject: this.$t('details.reject'),
                agree: this.$t('details.agree'),
                btnStatus: false,
                pShow: true
            };
        },
        created () {
            this.$router.setTitle(this.$t('title.checkDetails'));
            this.getDetails();
        },
        watch: {
            '$route' (to, from) {
                if (this.$route.name === 'check-details') {
                    this.postId = to.query.postId;
                    if (from.name === 'check-details') {
                        this.btnStatus = false;
                        this.reject = this.$t('details.reject');
                        this.agree = this.$t('details.agree');
                        this.getDetails();// 配合keep-alive,路由变化重新加载...
                    }
                }
            }
        },
        updated () {
            if (this.pShow) {
                if (document.querySelector('.article').getClientRects().length > 10) {
                    this.allShow = true;
                } else {
                    this.allShow = false;
                }
            }
        },
        methods: {
            getDetails () {
                let self = this,
                    params = {};
                params.postId = self.$route.query.postId;
                params.userId = self.$route.query.userId;
                publicPost(getApiUrl('checkDetails'), params).then((res) => {
                    document.getElementsByClassName('scrollTop')[0].scrollTop = 0;
                    self.connector = res.userId;
                    self.details = res;
                    res.descrimgs ? self.imgLists = JSON.parse(res.descrimgs) : self.imgLists = [];
                    self.recomList = res.recomList;
                }).catch((e) => {
                    self.$toast.center(e);
                });
            },
            seeAll () {
                this.pShow = false;
                this.allShow = false;
            },
            goApprove () {
                this.$router.push({name: 'approve-details', query: {userId: this.details.userId}});
            },
            goDetails (item) {
                let self = this;
                if (self.details.nextPostId > 0) {
                    self.$router.push({name: 'check-details', query: {postId: this.details.nextPostId, userId: this.$route.query.userId}});
                } else {
                    self.$toast.center(self.$t('details.last'));
                }
            },
            check (status) {
                let self = this,
                    params = {};
                params.postId = self.$route.query.postId;
                params.userId = self.$route.query.userId;
                params.suc = status;
                if (!this.btnStatus) {
                    publicPost(getApiUrl('checkSummit'), params, {loading: true}).then((res) => {
                        this.btnStatus = true;
                        if (status === '2') {
                            this.reject = this.$t('details.rejected');
                            this.agree = this.$t('details.agree');
                        } else if (status === '1') {
                            this.reject = this.$t('details.reject');
                            this.agree = this.$t('details.agreed');
                        }
                    }).catch((e) => {
                        self.$toast.center(e);
                    });
                }
            }
        }
    };
</script>

